nav {
  width: 100vw;
  height: 60px;
  z-index: 200;
  position: relative;
}

.nav-container {
  width: 80%;
  max-width: 1200px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  margin: auto;
}

.links {
  display: flex;
  justify-content: space-between;
  gap: 25px;
  align-items: center;
}

.links a {
  text-decoration: none;
  color: black;
}

.nav-images {
  height: 50px;
  align-self: self-end;
  display: flex;
}

.chatbox{
  height: 80%;
  
}

.nav-container img {
  height: 100%;
}

.icon {
  font-size: 30px;
}

.connect-wallett {
  background-color: aliceblue;
  font-size: 20px;
  font-weight: 400;
  padding: 5px 20px;
  border-radius: 20px;
  font-family: Arial, Helvetica, sans-serif;
  box-shadow: 0 0 10px 0px rgb(92, 146, 150);
  line-height: 24px;
  text-align: center;
  border: 1px solid black;
}

@media only screen and (max-width: 600px) {
  .nav-container img {
    display: none;
  }
  nav {
    height: auto;
  }
  .connect-wallet {
    padding: 5px 20px;
    margin: 10px 0 10px;
  }
  .icon {
    display: none;
  }
  .nav-container {
    justify-content: center;
  }
  .links {
    justify-content: center;
    gap: 0;
    align-items: center;
  }
}
