@font-face {
  font-family: Kristen;
  src: url(./KristenNormalITCStd.otf);
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  overflow: hidden;
}

body {
  background-color:#bdf4fd ;
  z-index: 2;
}

#main-body {
  /* display: none; */
  z-index: 0;
  width: 97vw;
  /* border: 2px solid black; */
  margin: auto;
  overflow: hidden;
  border-radius: 30px 30px 0 0;
  background-color: rgb(214, 235, 255);
  overflow-y: scroll;
  height: calc(100vh - 60px);
  position: relative;
  box-shadow: inset 0 0 20px 10px rgb(160, 172, 199);
}

.alertwin{
  position: absolute;
  top: 8vh;
  border: 1px solid rgb(229, 255, 0);
  background-color: rgba(238, 255, 0, 0.431);
  font-size: 20px;
  top: 100%;
  right: 5%;
  z-index: 5;
  transition: 200ms;
  width: 230px;
  text-align: center;
  color: rgb(0, 0, 0);
  padding: 5px 10px;
  display: none;
  animation: alert 3000ms;
}

@keyframes alert {
  0%{

  }
 10%{
  translate: 0 -150%;

 }
  90%{

    translate: 0 -150%;
  }

}

::-webkit-scrollbar {
  display: none;
}
@media only screen and (max-width: 992px) {
  #main-body {
    width: 95vw;
    height: calc(100vh - 54px);
  }
}
