#info {
  font-family: Kristen;
  background: linear-gradient(180deg, #e6edf4, #f1f6f8);
  box-shadow: 0 0 10px 10px #e6edf4;
  position: relative;
  padding-top: 200px;
  padding-bottom: 200px;
  z-index: 2;
}

.info {
  position: relative;

  width: 60%;
  display: flex;
  gap: 20px;
  margin: auto;
  align-items: center;
  opacity: 0;

  transition: 1000ms;
}

.paras {
  display: flex;
  flex-direction: column;
  gap: 30px;
  translate: -100%;
  transition: 500ms;
  color: black;
}

.paras h3 {
  font-size: 36px;
}

.paras p {
  font-size: 24px;
}

.info-img {
  width: 40%;
  border-radius: 25px;
  translate: 100%;
  transition: 500ms;
}
