#task-container,
.black {
  height: 100vh;
  width: 100vw;
  transition: 100ms;

}
#task-container {
  position: fixed;
  top: 0;
  z-index: 10;
}

.black {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.356);
}

.task {
  position: relative;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
  /* height: 70vh; */
  min-width: 250px;
  width: 40vw;
  border-radius: 20px;
  /* background-position: center; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding:15vh 0 15vh;
  align-items: center;
  gap: 50px;
 animation: comin 100ms;
}

@keyframes comin {
  from{
    scale:0.7;
  }
  to{
scale:1;
  }
}


.task form {
  display: flex;
  width: 80%;
  max-width: 500px;
  min-width: 230px;
  gap: 10px;
  flex-direction: column;
  align-items: center;
}

.task h3{
color:rgb(0, 0, 0);

}

.img-cutie{
position: absolute;
left: 10px;
top: 0;
translate: 0 -100%;
width: 150px;
}

.task a{
    color: white;
    background-color: rgb(0, 46, 77);
    padding: 10px;
    text-decoration: none;
    border-radius: 10px;
}

input[type="text"] {
  width: 100%;
  padding: 10px 10px;
  background-color:rgb(255, 255, 255);
  border: 1px solid black;
  outline: none;
  border-radius: 10px;
}
input[type="submit"]{
    width: 20%;
  padding: 10px 10px;
    border-radius: 10px;
    border: 1px solid black;
    background-color: rgb(215, 228, 255);
}

@media only screen and (max-width: 992px) {
  .task{
    min-width: 230px;
    width: 80vw;
  }
  .img-cutie{
  width: 90px;
  left: 0;
  }
  .task h3{
    color:rgb(0, 0, 0);
    width: 80%;
    text-align: center;
    }
    .task a{
      color: white;
      background-color: rgb(0, 46, 77);
      padding: 5px;
      text-decoration: none;
      border-radius: 10px;
      font-size: 16px;

  }
  .task h3{
    font-size: 16px;
line-height: 30px;
  }
  input[type="submit"]{
    width: 100%;

}
  }