#canvas {
  position: relative;
  height: calc(100vh - 0px);
}

.mountains {
  position: absolute;
  bottom: 0vh;

  z-index: 2;
  width: 100%;
}

.main-heading {
  font-family: Kristen;
  position: absolute;
  top: 35%;
  font-size: 48px;
  left: 60px;
  translate: 0 -50%;
  z-index: 1;
  min-width: 220px;
}

@media only screen and (max-width: 992px) {
  .main-heading {
  font-size: 40px;
    text-align: center;
    left: 50%;
    top: 40%;
    translate: -50%;
  }
  #canvas {
    height: calc(100vh - 60px);
  }

}
