#nft-showcase {
  margin-top: 0;
  position: relative;
  z-index: 2;
  background-color: #12527e;
  min-height: 100vh;
  box-shadow: 0 0 1px 3px #12527e;
  background: url(../../public/images/bottom.png),
    linear-gradient(#12527e, #12527e);
  background-position: 0 100%;
  background-size: 100%;
  background-repeat: no-repeat;
}
.showcases {
  position: absolute;
  left: 50%;
  top: 50%;
  translate: -50% -50%;
  display: flex;
  gap: 40px;
  flex-direction: column;
  /* justify-content: space-between; */
  align-items: center;
  transition: 300ms;
  flex-wrap: wrap;
}

.showcases-a,
.showcases-b {
  display: flex;
  gap: 20px;
  opacity: 0;
}

.showcases-a {
  translate: 200%;
  transition: translate 500ms;
}

.showcases-a div,
.showcases-b div {
  height: 250px;
  position: relative;
}

.showcases-b {
  translate: -200%;

  transition: translate 500ms;
}

.showcases-a .showcase {
  transform: rotateY(180deg);
}

.showcase {
  width: 250px;
  border-radius: 25px;
  transition: 300ms;
}

.showcase:hover {
  filter: brightness(0.7);
}

.tick {
  position: absolute;
  top: 100%;
  left: 100%;
  translate: -70% -70%;
  z-index: 2;
  width: 50px;
  font-size: 50px;
  color: rgb(35, 214, 154);
  scale: 1;
  background-color: #12527e;
  border-radius: 50%;
}


.hidden{
  display: none;
}

@media only screen and (max-width: 992px) {
  #nft-showcase {
    background-size: 300%;
    background-position: center 100%;
  }

  .tick{
    width: 25px;
    font-size: 25px;
  }

  .info {
    flex-direction: column-reverse;
    width: 90%;
  }
  .info img {
    width: 200px;
    border-radius: 25px;
  }
  .showcases-a div,
.showcases-b div {
  height: 100px;
  position: relative;
}
  .paras {
    text-align: center;
  }
  .showcase {
    width: 100px;
  }
  .showcases-a,
  .showcases-b {
    gap: 15px;
  }
  .showcases {
    gap: 20px;
  }
}
